.header {
  height: 80px;
  background: #4045BA;
}

.header__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header__logo a {
  font-size: 24px;
  line-height: 29px;
  font-weight: 600;
}

.header__nav ul {
  display: flex;
  font-weight: 600;
}

.header__nav ul li {
  padding: 8px 16px;
  border-radius: 10px;
  cursor: pointer;
}

.header__nav ul li a {
  color: #000000;
}

.header__nav ul li:first-child {
  background-color: #ffffff;
}

.header__nav ul li:first-child a {
  color: #4045BA;
}

.header__nav ul li:last-child {
  border: 1px solid #000000;
  background-color: #A549D0;
}

.header__nav ul li:nth-child(2),
.header__nav ul li:nth-child(3),
.header__nav ul li:nth-child(4),
.header__nav ul li:nth-child(5) {
  margin-left: 35px;
}

.header__nav ul li:nth-child(2) a,
.header__nav ul li:nth-child(3) a,
.header__nav ul li:nth-child(4) a,
.header__nav ul li:nth-child(5) a {
  color: #ffffff;
}

.header__nav ul li:nth-child(2):hover,
.header__nav ul li:nth-child(3):hover,
.header__nav ul li:nth-child(4):hover {
  background-color: #ffffff;
}

.header__nav ul li:nth-child(2):hover a,
.header__nav ul li:nth-child(3):hover a,
.header__nav ul li:nth-child(4):hover a {
  color: #4045BA;
}

body {
  font-family: "Gilroy", sans-serif;
  font-size: 14px;
}

.container {
  max-width: 1224px;
  width: 100%;
  padding: 0 10px;
  margin: 0 auto;
}

a {
  text-decoration: none;
  color: #ffffff;
}

ul {
  padding: 0;
  margin: 0;
}

li {
  list-style-type: none;
}
